.Modal {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.5);

    display: flex;
    flex-direction: column;
    align-items: center;
}

.Modal__container {
    position: relative;
    top: 10%;
    background-color: #ffffff;
    padding: 1rem;
    max-height: 80%;
    overflow-y: auto;
}

.Modal__close-button {
    position: absolute;
    top: 0;
    right: 0;
    border: 0;
    background-color: #f5f5f5;
    padding: 0.5rem 1rem;
}

.Modal__close-button:hover {
    background-color: rgb(255, 86, 86);
}
